<template>
  <div id="wallet" :style="{backgroundImage:'url('+bg+')'}">
    <!--用户信息-->
    <user></user>
    <!--记录列表-->
    <mt-navbar v-model="activeTab" class="moneyNavTab">
      <mt-tab-item id="1" class="sizeTxt" @click.native="toDay">今日</mt-tab-item>
      <mt-tab-item id="2" class="sizeTxt" @click.native="yesTwo">昨日</mt-tab-item>
      <mt-tab-item id="3" class="sizeTxt" @click.native="oneWeek">一周</mt-tab-item>
      <mt-tab-item id="4" class="sizeTxt" @click.native="oneMou">30天</mt-tab-item>
      <mt-tab-item id="5" class="sizeTxt" @click.native="upDown">上下分记录</mt-tab-item>
    </mt-navbar>
    <mt-tab-container v-model="activeTab" class="details">
      <!--今日钱包记录-->
      <mt-tab-container-item id="1" name="toDay">
        <div class="moneyList">
          <ul>
            <li v-for="(items,index) in  currentList" :key="index">
              <p><span class="surplusTxt">剩余金额：<b class="surplusTxtMoney">{{items.remainder}}</b></span>金钱：<b class="moneyTxt">{{items.change_money}}</b></p>
              <p><span class="bettingTime">{{items.change_date}}</span>{{items.remark}}</p>
            </li>
          </ul>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="2" name="yesTwo">
        <div class="moneyList">
          <ul>
            <li v-for="(items,index) in  currentList" :key="index">
              <p><span class="surplusTxt">剩余金额：<b class="surplusTxtMoney">{{items.remainder}}</b></span>金钱：<b class="moneyTxt">{{items.change_money}}</b></p>
              <p><span class="bettingTime">{{items.change_date}}</span>{{items.remark}}</p>
            </li>
          </ul>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="3" name="oneWeek">
        <div class="moneyList">
          <ul>
            <li v-for="(items,index) in  currentList" :key="index">
              <p><span class="surplusTxt">剩余金额：<b class="surplusTxtMoney">{{items.remainder}}</b></span>金钱：<b class="moneyTxt">{{items.change_money}}</b></p>
              <p><span class="bettingTime">{{items.change_date}}</span>{{items.remark}}</p>
            </li>
          </ul>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="4" name="oneMou">
        <div class="moneyList">
          <ul>
            <li v-for="(items,index) in  currentList" :key="index">
              <p><span class="surplusTxt">剩余金额：<b class="surplusTxtMoney">{{items.remainder}}</b></span>金钱：<b class="moneyTxt">{{items.change_money}}</b></p>
              <p><span class="bettingTime">{{items.change_date}}</span>{{items.remark}}</p>
            </li>
          </ul>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="5" name="upDown">
        <div class="moneyList">
          <ul>
            <li v-for="(items,index) in  currentList" :key="index">
              <p><span class="surplusTxt">剩余金额：<b class="surplusTxtMoney">{{items.remainder}}</b></span>金钱：<b class="moneyTxt">{{items.change_money}}</b></p>
              <p><span class="bettingTime">{{items.change_date}}</span>{{items.remark}}</p>
            </li>
          </ul>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
    <div class="prePage" @click="prePage" v-show="currentPage!=1">上一页</div>
    <div class="nextPage" @click="nextPage" v-show='currentPage >= 1 && total>currentPage*pageNumber'>下一页</div>
    <!-- 底部导航菜单 -->
    <tabs :selected="selected"></tabs>
  </div>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'wallet',
  data () {
    return {
      bg: require('./../assets/images/game21.png'), // 背景图片
      // 分页信息
      currentPage: 0, // 多少页
      pageNumber: 10, // 每页显示20条数据
      activeTab: '1', // 今日，昨日...选项卡
      moneyList: [],
      currentList: [],
      selected: '',
      id: localStorage.getItem('id'), // 缓存取出用户ID
      total: 0
    }
  },
  created () {
    // 钱包记录今日接口
    this.currentPage++
    this.api(this.currentPage, this.pageNumber, this.activeTab)
  },
  components: {
    tabs, user
  },
  methods: {
    toDay () {
      this.currentPage = 1
      this.activeTab = '1'
      this.api(this.currentPage, this.pageNumber, this.activeTab)
    },
    yesTwo () {
      console.log(this)
      this.currentPage = 1
      this.activeTab = '2'
      this.api(this.currentPage, this.pageNumber, this.activeTab)
    },
    oneWeek () {
      this.currentPage = 1
      this.activeTab = '3'
      this.api(this.currentPage, this.pageNumber, this.activeTab)
    },
    oneMou () {
      this.currentPage = 1
      this.activeTab = '4'
      this.api(this.currentPage, this.pageNumber, this.activeTab)
    },
    upDown () {
      this.currentPage = 1
      this.activeTab = '5'
      this.api(this.currentPage, this.pageNumber, this.activeTab)
    },
    api (pageNum, pageSize, seldate) {
      this.$axios.post('http://www.xv88.cn/Lottery/Controllers/DataDealing.ashx?action=stream', {
        pid: this.id,
        pageNum,
        pageSize,
        seldate
      })
        .then(data => {
          this.currentList = data.data.rows
          this.total = data.data.total
        })
    },
    prePage () {
      this.currentPage--
      this.api(this.currentPage, this.pageNumber, this.activeTab)
    },
    nextPage () {
      this.currentPage++
      this.api(this.currentPage, this.pageNumber, this.activeTab)
    }
  }
}
</script>

<style scoped>
  .prePage {position: fixed;top: 50%;transform: translateY(-50%);left: 0;background: rgba(255, 0, 0, 0.7);color: #fff;width: 20px;padding: .6rem;z-index:999;border-radius: 0 15px 15px 0;border: 1.5px solid #fff;}
  .nextPage {position: fixed;top: 50%;transform: translateY(-50%);right: 0;background: rgba(255, 0, 0, 0.7);color: #fff;width: 20px;padding: .6rem;z-index: 999;border-radius: 15px 0px 0 15px;border: 1.5px solid #fff;}
  .hide {display: none;}
  #wallet{ overflow-y: scroll !important; background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}

  /*钱包记录列表样式*/
  .moneyNavTab{background: #fff8de;width: 94%;border-radius: 4px;margin: 0 auto;overflow: hidden;}
  .moneyNavTab  .mint-tab-item.is-selected{background: #e96439}
  .moneyNavTab .sizeTxt{border-right: 1px solid #e96439; color: #3c2611;}
  .details{display: inline-block; width: 100%; border-top: 1px solid #ceb0ac;margin-top: 0.6rem;height: 64%;overflow-y: scroll !important;}
  .moneyList ul{display: inline-block; width: 100%; font-size: 0.8rem; color: #fff; width: 100%; }
  .moneyList ul li{border-bottom: 1px solid #9c6564; padding: 0.2rem 3% }
  .moneyList ul li p:nth-child(1){margin-bottom: 0.3rem;}
  .moneyList ul li p .moneyTxt{color: #6ffab7;font-weight: 500; font-size: 0.8rem;}
  .moneyList ul li p .surplusTxt{float: right;}
  .moneyList ul li p .surplusTxtMoney{font-weight: 500; font-size: 0.8rem; color: #d9c2ca;}
  .moneyList ul li p:nth-child(2){font-size: 0.7rem; color: #d9c2ca;}
  .moneyList ul li p .bettingTime{float: right;}
</style>



